// An highlighted block
<template>
	<view class="content">
		<view class="avatorWrapper">
			<image class="img" mode="widthFix" src="/static/img/hzau.png"></image>
		</view>

		<view class="form">
			<view class="inputWrapper">
				<view class="inputTip">
					<text>电话/邮箱：</text>
				</view>
				<view class="input">
					<input class="baseInput" type="text" v-model="account" />
				</view>
			</view>
			<view class="inputWrapper">
				<view class="inputTip">
					<text>密码：</text>
				</view>
				<view class="input">
					<input class="baseInput" type="password" v-model="password" />
				</view>
			</view>
			<view class="inputWrapper">
				<view class="inputTip">
					<text>确认密码：</text>
				</view>
				<view class="input">
					<input class="baseInput" type="password" v-model="passwordAgain" />
				</view>
			</view>
			<view class="inputWrapper">
				<view class="inputTip">
					<text>验证码：</text>
				</view>
				<view class="input">
					<input class="baseInput" type="number" v-model="checkCode" />
					<view class="seconds" @click="sendCheckCode">
						<text>{{seconds ? seconds : '发送'}}</text>
					</view>
				</view>
			</view>
			<view class="btn" @click="register">
				<text>完成注册</text>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		regist,
		sendCode
	} from '@/api/login'
	import {
		testLoginInfo
	} from '@/api/login/loginInfo.js'
	export default {
		data() {
			return {
				account: '',
				password: '',
				passwordAgain: '',
				checkCode: '',
				seconds: 0,
			}
		},

		methods: {
			// 发送验证码
			sendCheckCode() {
				if (this.seconds > 0) return
				if (!testLoginInfo(this.account, this.password, this.passwordAgain)) return
				// 给后台发送验证码
				sendCode(this.account)
				// 计时器秒数
				this.seconds = 60
				const secondsID = setInterval(() => {
					if (this.seconds === 0) return clearInterval(secondsID)
					this.seconds--
				}, 1000)
			},

			// 注册
			register() {
				// 检查输入
				if (!testLoginInfo(this.account, this.password, this.passwordAgain)) return
				regist({
					account: this.account,
					password: this.password,
					checkCode: this.checkCode,
					success() {
						setTimeout(() => {
							uni.navigateTo({
								url: `/pages/login/login?account=${this.account}&password=${this.password}`
							})
						}, 500)
						// 保存账号信息到本地，方便下次登录
						uni.setStorageSync('account', this.account)
						uni.setStorageSync('password', this.password)
					},
				})
			},
		}
	}
</script>

<style lang="less">
	@import '../../static/css/base.less';

	.input {
		position: relative;

		.seconds {
			position: absolute;
			top: 0;
			right: 4%;
			padding: 20rpx 30rpx;
			color: #888;
		}
	}
</style>
